body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F9F9F9;
}

.list-wrapper {
  margin: 40px 40px 20px 270px;
  width: 35%;
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0px 0px 26px 1px rgba(0, 0, 0, 0.39);
  border-radius: 7px 7px 7px 7px;
  border: 0px solid #000;
  overflow-y: scroll;
}
.list-wrapper ul {
  padding: 0;
  margin: 0;
}
.list-wrapper h1 {
  font-weight: normal;
  text-align: left;
  margin-top: 10px;
  margin-bottom: 0px;
}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

input {
    width: 70%;
	height: 30px;
	border-radius: 3px;
	border: 2px solid #CCCCCC;
	font-size: 14px;
	padding-left: 10px;    
    outline-width: medium;
    -webkit-align-self: flex-start;
            align-self: flex-start;
}        

input::-webkit-input-placeholder {
    font-size: 14px;
}

.list-input button {
	margin-top: 30px;
	width: 20%;
	height: 37px;
	background: #27CCC0;
	color: white;
	font-size: 14px;
	border: 1px solid #27CCC0;
    border-radius: 6px;
    margin-left: 37px;
}    

.list-input button:hover {
		cursor: pointer;
}	

.list-input {
	margin-bottom: 20px;
}
.list-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin: 15px 0;
    padding-bottom: 15px;
    border-bottom: 1px solid #F2F2F2;
    -webkit-align-content: center;
            align-content: center;
}
.list-item button {
    width: 97px;
    height: 34px;
    border: 2px solid #FF3159;
    color: #FF3159;
    background-color: #fff;
    border-radius: 5px;
    float: right;
    display: inline-block;
    cursor: pointer;
    line-height: inherit;
    touch-action: manipulation;
    overflow: visible;
    text-transform: none;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    text-align: center;
}
.list-item .button-container {
    -webkit-flex-grow: 2;
            flex-grow: 2;
    text-align: right;
  }

.list-item button:hover {
    background: #f44336;
    color: white;
    cursor: pointer;
}
.list-item h3 {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 300px;
    float: left;
    font-weight: normal;
}

.list-item span {
    background-color: #27CCC0;
    color: #fff;
    padding: 3px 10px;
    border-radius: 10%;
    cursor: pointer;
    font-size: 13px;
    margin-left: 20px;
}
.list-item:hover {
    cursor: pointer;
}

.sidebar {
    height: 100%;
    width: 15%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #2B2F3E;
    padding-top: 20px;
    text-align: center;
}
.sidebar h1 {
    font-weight: normal;
    color: white;
}
.sidebar p {
    color: darkgray;
}  


.comments-section {
    box-shadow: 0px 0px 26px 1px rgba(0, 0, 0, 0.39);
    border-radius: 7px 7px 7px 7px;
    border: 0px solid #000000;
    background-color: #ffffff;
    width: 35%;
    padding: 20px;
    margin-top: 40px;
    overflow-y: scroll;
  }

.comments-section li {
    list-style: none;
    margin: 20px 0;
    padding-bottom: 15px;
    border-bottom: 1px solid #F2F2F2;
}

.comments-section h1{
    font-weight: normal;
}
.comment-input input{
    height: 80px;
    width: 80%;
    display: inline-block;
}
.avatar{
    background-color: #E6E6E6;
    width:52px;
    height:52px;
    display: inline-block;
}
